body {
    font-family: Roboto, sans-serif;
}

.app-wrapper {
    display: grid;
    grid-template-areas:
          'h h'
          'n p';
    grid-template-rows: 150px auto;
    grid-template-columns: 300px 1fr;
    grid-gap: 10px;
    width: 1440px;
    margin: 20px auto;

}

.app-content {
    background-color: #FBEAFF;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;

}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

input {
    outline: none;
}
